<template>
  <div>
    <b-card class="card-custom card-stretch gutter-b">
      <vxe-toolbar>
        <template #buttons>
          <vxe-input
            v-model="filterName"
            type="search"
            placeholder="Please input key word here"
            @keyup="searchEvent"
          ></vxe-input>
        </template>
      </vxe-toolbar>
      <vxe-grid
        v-bind="gridOptions"
        :cell-style="cellStyle"
        auto-resize
      ></vxe-grid>

      <!-- <b-table striped hover :items="tableData"></b-table> -->
    </b-card>
  </div>
</template>

<script>
// import XEUtils from 'xe-utils'
import XEUtils from "xe-utils";
import Vue from "vue";
import VXETable from "vxe-table";
import "vxe-table/lib/style.css";
Vue.use(VXETable);

export default {
  name: "widget-paperlist",
  data() {
    var result_json = require("../../../../../public/Tasks/" +
      this.$store.state.taskinfo.name +
      "/paper_list.json");
    var table_column = [
      { title: "paper", field: "paper", type: "html"},
      { title: "code", field: "code" },
      {
        title: "Evaluate in this dataset",
        children: [],
        minWidth:100,
      },
    ];
    // var dataset_column_info = ;

    for (var item of result_json.header) {
      if (item != "paper" && item != "code" && item != "link") {
        table_column[2].children.push({
          title: item,
          field: item,
        });
      }
    }
    // table_column.push(dataset_column_info)
    var gridOptions = {
      border: true,
      resizable: true,
      showOverflow: true,
      height: 500,
      align: "center",
      filterName: '',
      //   headerAlign:"center",
      columns: table_column,
      data: result_json.content,
    };
    return {
      filterName: '',
      list: [],
      tableColumn: table_column,
      tableData: result_json.content,
      result_json: result_json,
      gridOptions: gridOptions,
    };
  },
  methods: {
    searchEvent() {
      const filterName = XEUtils.toValueString(this.filterName)
        .trim()
        .toLowerCase();
      if (filterName) {
        const filterRE = new RegExp(filterName, "gi");
        const searchProps = ["paper", "code"];
        const rest = this.tableData.filter((item) =>
          searchProps.some(
            (key) =>
              XEUtils.toValueString(item[key])
                .toLowerCase()
                .indexOf(filterName) > -1
          )
        );
        this.gridOptions.data = rest.map((row) => {
          const item = Object.assign({}, row);
          searchProps.forEach((key) => {
            item[key] = XEUtils.toValueString(item[key]).replace(
              filterRE,
              (match) => `<span class="keyword-lighten">${match}</span>`
            );
          });
          return item;
        });
      } else {
        this.gridOptions.data = this.tableData;
      }
    },
  },
  //   mounted() {
  //     this.$refs.xGrid.reloadData(this.result_json.content);
  //   },
};
</script>
<style>
.keyword-lighten{
    color: rgba(0, 143, 251, 0.85);
}
</style>
